        return {
            i18n
        };
    },
    data() {
        return {
            mobileBg: this.$store.state.viewListen
        };
    },
    mounted() {
        window.addEventListener("scroll", anima); //监听滚动触发anima函数
    },
    beforeRouteLeave(to, form, next) {
        // 离开路由移除滚动事件
        window.removeEventListener("scroll", anima);
        next();
    },
    methods: {
        goReception() {
            window.localStorage.setItem("path", "/reception");
